@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;

}



body {
  font-family: 'Outfit', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.3rem;

}
h1 { 
  font-size: 6rem;
}

.app {
  width: 100%;
  height: 103vh;
  position: relative;
  background-color: rgba(0, 0, 0, 0.5);
  color: #fff;

}

.app:before {
  content: '';
  background: url("https://source.unsplash.com/2000x1500/?skyview") no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
  
}

.app .search {
  text-align: center;
  padding: 2.8rem;
}

.app .cityname {
  font-size: 2rem;
  font-weight: 500;
}

.app input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255, 255, 255, 0.747);
  background: rgba(255, 255, 255, 0.1);
  color: #fff;
}

::placeholder {
  color: #fff;
}


.container {
  max-width: 700px;
  height: 520px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.app .top {
  width: 100%;
  margin: 1rem auto;

}

.app .description {
  position: relative;
  right: -90%;
  transform-origin: 0 0;
  transform: rotate(269deg);

}

.app .bottom {
  display: flex;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 1rem auto;
  padding: 1rem;
  border-radius: 12px;
  background-color: rgba(255, 255, 255, 0.2) ;

}

.bold {
  font-weight: 700;
  font-size: 1.5rem;
}